.popup__container {
  max-width: 430px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  color: #000000;
  box-sizing: border-box;
  position: relative;
  padding: 34px 0 37px 0;
}

@media screen and (max-width: 650px) {
  .popup__container {
    max-width: 282px;
    padding: 25px 0 25px 0;
  }
}
